import { useState } from 'react';
import React from 'react';

function TextBucks(props: any) {
    const [count, setCount] = useState(props);
    return (
    <>
        <p>{count}</p>
        <button onClick={() => setCount(count + 1)}>
        Click me
        </button>
    </>
    );
    //return <h1>Hello, {props}</h1>;
}
export default TextBucks;