import './App.css';
import TextBucks from './TextBucks';
import { useState } from 'react';
import './styles/validation.css';


function App() {
  const [count, setCount] = useState(0);
  const [mySpecs, setMySpecs] = useState({
    Upper: 0, 
    Lower: 0
  });

  return (
    <div className="App">
      <header className="App-header">
        <p className={` ${isValid(mySpecs)} `}>{count}</p>
        <button onClick={() => {
          setCount(count + 1)
          setMySpecs({Upper: count, Lower: count })
        }}>
        Click me
        </button>
        <hr style={{
          width: 100
        }}/>
        {TextBucks(count)}
      </header>
    </div>
  );
}

interface SpecLimit {
  Upper: number;
  Lower: number;
}

function isValid(specLimit: SpecLimit) : string
{
  let ret: string;
  if(specLimit.Upper > 10) {
    ret = "error";
  }
  else if (specLimit.Lower < 5) {
    ret = "warning";
  }
  else {
    ret = "good";
  }

  return ret;
}


export default App;
